.btm{
    position: fixed;
    width: 100%;
    bottom: 0;
    height:40px;
    padding:0;
    box-shadow:rgba(0, 0, 0, 0.15) 0 -0.01rem 0.06rem 0
}

.leftbtn{
    float: left;
    width: 15%;
    height: 80%;
    text-align:center;
    color: #999;
    font-size:12px;
    margin-top:4px;
}

.cart{
    float: left;
    width: 27.5%;
    height: 80%;
    color: #fff;
    font-size: 14px;
    border-radius: 1rem 0 0 1rem;
    background-image: -webkit-linear-gradient(left, #FFC500, #FF9402);
    text-align: center;
    line-height: 32px;
    font-weight: bold;
    margin-top:4px;
}

.buy{
    float: left;
    width: 27.5%;
    height: 80%;
    color: #fff;
    font-size: 14px;
    border-radius: 0 1rem 1rem 0;
    background-image: -webkit-linear-gradient(left, #FF7A00, #FE560A);
    text-align: center;
    line-height: 32px;
    font-weight: bold;
    margin-top:4px;
}
