.ddiv{
    background: #fff;
}
.loginHead{
    width: 100%;
    height: 52px;
    background: rgb(238, 238, 238);
    line-height: 52px;
    text-align: left;
    box-sizing: border-box;
    padding-left: 20px;
    position: absolute;
    left: 0px;
    top: 0px;
    font-size: 16px;
    font-weight: bold;
    color: rgb(51, 51, 51);
}

.close{
    display: block;
    position: absolute;
    right: 0px;
    top: 0px;
    height: 52px;
    line-height: 52px;
    padding: 0px 20px;
    color: rgb(153, 153, 153);
    text-decoration: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    font-size: 16px;
    font-weight: bold;
}

.tb-logo{
    background: url(https://gw.alicdn.com/tfs/TB1p.BGQXXXXXbFXFXXXXXXXXXX-160-160.png) no-repeat;
    background-size: contain;
    width: 80px;
    height: 80px;
    margin: 60px auto 0;
    box-sizing: border-box;
    padding: 0;
}

.loginForm{
    margin: 50px auto 0;
    width: 90%;
}

.id-div{
    border-bottom: 1px solid #ff5000;
    margin: 40px 0 0;
}

.loginId{
    border:0;
    padding-left: 5px;
    font-size: 16px;
    color: #333;
    height: 30px;
    border-radius: 0;
    width: 100%;
    display: inline-block;
    text-align: start;
    font:Arial;
}

.pwd-div{
    border-bottom: 1px solid #ff5000;
    margin: 40px 0 0;
}

.loginPwd{
    width: 100%;
    border: 0;
    padding-left: 5px;
    font-size: 16px;
    color: #333;
    height: 30px;
    font:Arial;
}

.codeLogin{
    color: #555;
    font-size: 14px;
    margin-left: 0;
    float: left;
}

.yanzheng-div{
    margin-top: 20px;
    text-align: right;
    zoom: 1;
}

.linkLogin{
    color: #555;
    font-size: 14px;
    margin-left: 10px;
}

.loginBtn{
    background: -webkit-linear-gradient(left,#ff9000,#ff5000) no-repeat;
    color: #fff;
    box-shadow: 0 2px 4px #f7c7b1;
    height: 45px;
    border-radius: 35px;
    margin-top: 25px;
    font-size: 16px;
    border: 1px solid #ff9000;
    line-height: 42px;
    width: 100%;
}